body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background: #CFD8DC;
  align-items: center;
  font-size: 12px;
  color: black;
}

select {
  border: 1px solid blue;
  font-size: 14px;
  border-radius: 4px;
  background-color: orange;
}

select:hover {
  border: 1px solid blue;
  border-radius: 4px;
  background-color: rgb(246, 218, 166);
}


table {
  border-collapse: collapse
}

th {
  text-align: center;
  border: 1px solid #333;
  overflow: hidden;
}

td {
  border: 1px solid #333; 
  height: 30px;
}

thead {
  background: #78909C;
}


#innerDiv {
  width: 99%;
}

.no-highlight {
  background: #ECEFF1;
}
.highlighted {
  background: #FFFF00;
}


h1 {
  font-size: 20px
}

h2 {
  font-size: 16px;
}


.topnav {
  position: fixed;
  top: 0;
  margin:0;
  overflow: hidden;
  width: 100%;
  
}

.headerBox {
  display: flex;
  flex-wrap: wrap;
  width:100%;
  background-color: #78909C;
  justify-content: center;
}

.menuBox {
  background-color: #607D8B;
  font-size: 14px;

  display: flex;
  justify-content: space-around;
}


.navhead {
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
}
.navhead div {
  width: 20%;
  color: #263238;
  font-size: 20px;
}

.navhead > h1 {
  width: 80%;
}
